import { useState, useEffect, useCallback } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlay } from '@fortawesome/free-solid-svg-icons/faPlay'
import './TextReader.css';

const TextReader = ({ init, isWaiting, setIsWaiting, chat, setChat, userQuestion, setUserQuestion, question, synth, selectedVoice, setQuestion }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [isButtonVisible, setIsButtonVisible] = useState(true); 


  const speakText = useCallback(() => {
    if (synth.speaking) {
      synth.cancel();
    }
    if (!isWaiting) {
      console.log('isWaiting is false');
      return;
    }
    if (!question) {
      handleNoQuestion();
      return;
    }
    prepareSynthSettings();
    if (!synth || !selectedVoice) {
      console.error('Synth or selectedVoice not initialized');
      return;
    }
    addToChat(question);
    const chunks = splitIntoChunks(question);
    speakChunks(chunks);
  }, [synth, selectedVoice, question]);
  
  const handleNoQuestion = () => {
    console.log('no question');
    setIsPlaying(false);
    setIsWaiting(false);
  };
  
  const prepareSynthSettings = () => {
    synth.pitch = 1;
    synth.rate = 1;
    synth.volume = 1;
    // console.log('speakText');
    // console.log('firstQuestion:', firstQuestion);
    // console.log('synth:', synth);
    // console.log('selectedVoice:', selectedVoice);
  };
  
  const addToChat = (currQuestion) => {
    chat.push({ 'role': "assistant", 'message': currQuestion });
    console.log('currQuestion: ', currQuestion);
  };
  
  const splitIntoChunks = (text) => {
    let chunks = text.match(/[^.!?]+[.!?]+/g) || [text];
    return chunks.map(chunk => chunk.split(':')[0]);
  };
  
  const speakChunks = (chunks) => {
    const speakChunk = (chunkIndex) => {
      if (chunkIndex >= chunks.length) {
        setIsPlaying(false);
        setIsWaiting(false);
        console.log('\n\n\n::CHAT onend :::', chat, '\n\n\n');
        return;
      }
      console.log('speaking chunk', chunkIndex);
      const utterance = new SpeechSynthesisUtterance(chunks[chunkIndex]);
      utterance.voice = selectedVoice;
      utterance.onend = () => speakChunk(chunkIndex + 1);
      utterance.onerror = (e) => {
        console.error('Speech synthesis error:', e);
        setIsPlaying(false);
        setIsWaiting(false);
      };
      // console.log('inside ::::  synth ', synth, '  \nselectedvoice', selectedVoice);
      console.log('speak utterance.');
      synth.speak(utterance);
    };
  
    setIsPlaying(true);
    setIsWaiting(true);
    speakChunk(0);
  };

  useEffect(() => {
    // console.log('TextReader useEffect synth', synth);
    // if (synth) {
      speakText();
    // }
  }, [question, speakText]);

  return (
    <div className="response">
      {isButtonVisible && (
        <button className="start-button" onClick={() => {
          setIsButtonVisible(false); // Hide the button after it's clicked
          // setQuestion('Hello, welcome to Turning Pages Digital! Use the green microphone button near the bottom of the screen when you want to talk.\n\n If my questions are too easy or too difficult, just let me know. Please don\'t use one word answers as they are difficult for me to hear. For example, if I ask you to read a word like "cat", say something like, "The word is cat". Do you want to start talking about words or sentences?');
          // setQuestion('If I ask you to read a word like "cat", say something like, "The word is cat". Do you want to start talking about words or sentences?');
          setQuestion('Do you want to start talking about words or sentences?');
          setIsWaiting(true);
        }}>
          <FontAwesomeIcon className="big" color="#6f7" icon={faPlay} />
        </button>
      )}
      <p>{question}</p>
    </div>
  );
};

export default TextReader;